<style>
  
</style>

<template>
  <div>
    <div class="search-form-wraper">
      <el-form :model="searchContent" label-position="top">
        <el-form-item label="门店">
          <el-select class="search-input" v-model="searchContent.storeNo" filterable placeholder="请选择门店" clearable size="small" @change="doSearchHandel(true)">
            <el-option
              v-for="item in storeOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="doSearchHandel(true)" size="mini">搜索</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="table-wraper">
      <div class="sp-handel-w">
        <el-button size="mini" class="sup-mini" :disabled="this.checkedList.length === 0 || this.isDisabled" icon="el-icon-circle-check" @click="batchHandel('10')">启用自提</el-button>
        <el-button size="mini" class="sup-mini" :disabled="this.checkedList.length === 0 || this.isDisabled" icon="el-icon-close" @click="batchHandel('01')">禁用自提</el-button>
        <el-button size="mini" class="sup-mini" :disabled="this.checkedList.length === 0 || this.isDisabled" icon="el-icon-circle-check" @click="batchHandel('20')">启用外卖</el-button>
        <el-button size="mini" class="sup-mini" :disabled="this.checkedList.length === 0 || this.isDisabled" icon="el-icon-close" @click="batchHandel('02')">禁用外卖</el-button>
        <el-button size="mini" class="sup-mini" :disabled="this.checkedList.length === 0 || this.isDisabled" icon="el-icon-circle-check" @click="batchHandel('30')">启用配送</el-button>
        <el-button size="mini" class="sup-mini" :disabled="this.checkedList.length === 0 || this.isDisabled" icon="el-icon-close" @click="batchHandel('03')">禁用配送</el-button>
        <el-button size="mini" class="sup-mini" :disabled="this.checkedList.length === 0 || this.isDisabled" icon="el-icon-setting" @click="showSetFrightDialog">设置运费</el-button>
      </div>
      <el-table 
        ref="multipleTable" 
        class="fixed-height" 
        v-loading="loading" 
        :data="tableData" 
        empty-text="没有查询到您想要的数据"
        @select="handleSelect" 
        @select-all="handleSelectAll" 
        style="width:100%;" 
        size="small">
        <el-table-column type="selection" align="center"></el-table-column>
        <el-table-column label="门店信息" min-width="200px">
          <template slot-scope="scope">
            <div>
              <p>
                {{scope.row.storeNo}}-{{scope.row.storeName}}
              </p>
              <p class="mh-address">{{scope.row.address}}</p>
            </div>
          </template>  
        </el-table-column>
        <el-table-column label="自提" min-width="90px">
          <template slot-scope="scope">
            <div>
              <i style="color: #3080fe;" class="el-icon-circle-check" v-if="scope.row.selfPickFlag === 1"></i>
              <i class="el-icon-close" v-else></i>
            </div>
          </template>  
        </el-table-column>
        <el-table-column label="外卖配送" min-width="90px">
          <template slot-scope="scope">
            <div>
              <!-- 外卖0未启用1启用 -->
              <i style="color: #3080fe;" class="el-icon-circle-check" v-if="scope.row.takeawayFlag === 1"></i>
              <i class="el-icon-close" v-else></i>
            </div>
          </template>  
        </el-table-column>
        <el-table-column label="快递配送" min-width="90px">
          <template slot-scope="scope">
            <div>
              <i style="color: #3080fe;" class="el-icon-circle-check" v-if="scope.row.expressDeleiveryFlag === 1"></i>
              <i class="el-icon-close" v-else></i>
            </div>
          </template>  
        </el-table-column>
        <el-table-column min-width="150px" prop="orderId" label="运费设置">
          <template slot-scope="scope" size="small">
            <div v-if="scope.row.basicShippingFee === 0 && scope.row.freeShippingAmount === 0">
              免运费
            </div>
            <div v-else>
              <p>基础运费{{scope.row.basicShippingFee}}元</p>
              <p>满{{scope.row.freeShippingAmount}}元免运费</p>
            </div>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页条 -->
      <el-pagination v-if="total>0" class="tc mt20" background @size-change="changeSize" @current-change="changeCurrent" :current-page.sync="pageNum" :page-sizes="[10, 50, 100, 150, 200]" layout="total, sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>
    </div>
    <el-dialog title="批量修改运费" :visible.sync="dialogFormVisible" width='420px'>
      <el-form :model="setFreightParams" label-width="100px">
        <el-form-item label="基础运费" required>
          <el-input v-model="setFreightParams.basicShippingFee" class="search-input" size="small" placeholder="请输入金额"></el-input>
          <span>元运费</span>
        </el-form-item>
        <el-form-item label="单笔订单满" required>
          <el-input v-model="setFreightParams.freeShippingAmount" class="search-input" size="small" placeholder="请输入金额"></el-input>
          <span>元，免运费</span>
        </el-form-item>
        <div><span style="color: red;margin-left: 100px;">*注意: </span>均设为0元，则为免运设置</div>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false" size="mini">取 消</el-button>
        <el-button type="primary" size="mini" :disabled="isDisabled" @click="doSetFreightHandel">确定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import * as utils from '@/utils/utils'
  const salePriceReg = /((^[1-9]\d*)|^0)(\.\d{0,2}){0,1}$/
  const requestUrl = {
    // 获取门店列表接口
    search: '/plateform/a/store/condition/search',
    // 门店配送配置列表
    storeDdlList: '/plateform/a/mall/storeDelivery/list',
    // 关闭快递
    closeExpress: '/plateform/a/mall/storeDelivery/closeExpress',
    // 开启快递
    openExpress: '/plateform/a/mall/storeDelivery/openExpress',
    // 关闭自提
    closeSelfPick: '/plateform/a/mall/storeDelivery/closeSelfPick',
    // 开启自提
    openSelfPick: '/plateform/a/mall/storeDelivery/openSelfPick',
    // 关闭外卖
    closeTakeaway: '/plateform/a/mall/storeDelivery/closeTakeaway',
    // 开启外卖
    openTakeaway: '/plateform/a/mall/storeDelivery/openTakeaway',
    // 设置运费
    expressMoneyConfig: '/plateform/a/mall/storeDelivery/expressMoneyConfig'
  }
  export default {
    data() {
      return {
        isDisabled: false,
        loading: false,
        pageNum: 1,
        pageSize: 10,
        total: 0,
        searchContent: {
          storeNo: ''
        },
        storeOptions: [],
        tableData: [],
        checkedList: [],
        dialogFormVisible: false,
        setFreightParams: {
          basicShippingFee: '', // 基础运费
          freeShippingAmount: '', // 单笔免运费条件
          storeCodes: []
        }

      }
    },
    created() {
      this.getStoreList()
      this.doSearchHandel(true)
    },
    methods: {
      /**
       * [changeSize description]
       * @param    {[type]}                 value [description]
       * @return   {[type]}                       [description]
       */
       changeSize(value) {
        this.pageSize = value
        this.doSearchHandel()
      },
      /**
       * [changeCurrent 点击分页条]
       * @param    {[type]}                 value [当前页]
       * @return   {[type]}                       [description]
       */
      changeCurrent(value) {
        this.pageNum = value
        this.doSearchHandel()
      },
      /**
       * [getStoreList 获取门店列表]
       * @return {[type]} [description]
       */
      getStoreList() {
        this.$http.post(requestUrl.search).then(res => {
          if(res.code === 0) {
            if(res.data && res.data.length) {
              let optionsArr = []
              for(let item of res.data) {
                let optionItem = {
                  value: item.storeNo,
                  label: item.storeNo +'-'+ item.storeName
                }
                optionsArr.push(optionItem)
              }

              this.storeOptions = optionsArr
            }else {
              this.storeOptions = []
            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("提示(获取门店列表):", e.message)
        })
      },
      /**
       * [doSearchHandel 搜索]
       * @return {[type]} [description]
       */
      doSearchHandel(_param) {
        this.checkedList = []
        if(_param === true) {
          this.pageNum = 1
        }
        this.loading = true
        this.$http.post(requestUrl.storeDdlList, {...this.searchContent, pageSize: this.pageSize, pageNum: this.pageNum}).then(res => {
          this.loading = false
          if(res.code === 0) {
            if(res.data && res.data.result && res.data.result.length) {
              this.tableData = res.data.result
              this.total = res.data.total
            }else {
              this.tableData = []
              this.total = 0
            }
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * [handleSelect 选择某一个]
       * @return {[type]} [description]
       */
      handleSelect(val, row) {
        let hash = {}
        let checkedListTemp = []

        if (utils.isKeyInArray(row.storeNo, this.checkedList)) {
          this.checkedList = utils.removeItemByKeyVal('storeNo', row.storeNo, this.checkedList)
          return
        } else {
          checkedListTemp.push(row)
        }

        let checkedListTempArr = [...checkedListTemp, ...this.checkedList]
        this.checkedList = checkedListTempArr.reduceRight((item, next) => {
          hash[next.storeNo] ? '' : hash[next.storeNo] = true && item.push(next)
          return item
        }, [])
      },
      /**
       * [handleSelectAll 选择多个]
       * @return {[type]} [description]
       */
      handleSelectAll(val) {
        let hash = {}
        if (val.length > 0) {
          // 全选
          for (let item of val) {
            this.checkedList.push(item)
          }
          // 去重
          this.checkedList = this.checkedList.reduceRight((item, next) => {
            hash[next.storeNo] ? '' : hash[next.storeNo] = true && item.push(next)
            return item
          }, [])
        } else {
          // 全不选 此时val.length<=0
          // 1.获取当前table中每一条的id
          let checkedListTemp = []
          for (let item of this.tableData) {
            checkedListTemp.push(item)
          }
          // 2.从要传递的参数中去掉这些重复的
          let checkedListArr = utils.getSubtract(this.checkedList, checkedListTemp, 'storeNo')
          this.checkedList = checkedListArr && checkedListArr.length > 0 ? checkedListArr : []
        }
      },
      /**
       * @description: 批量操作
       * @param {type} 
       * @return: 
       */
      batchHandel(type) {
        // 10: 启用自提   01: 关闭自提    20: 启用外卖   02: 禁用外卖   30: 启用配送   03: 禁用配送
        let reqUrl = ''
        let tipMsg = ''
        let optTip = ''
        let storeCodes = this.checkedList.map(item => {
          return item.storeNo
        })
        switch(type) {
          case '10':
            reqUrl = requestUrl.openSelfPick
            tipMsg = '启用自提成功'
            optTip = '确定要将所选门店启用自提吗？'
          break;
          case '01':
            reqUrl = requestUrl.closeSelfPick
            tipMsg = '禁用自提成功'
            optTip = '确定要将所选门店禁用自提吗？'
          break;
          case '20':
            reqUrl = requestUrl.openTakeaway
            tipMsg = '启用外卖成功'
            optTip = '确定要将所选门店启用外卖吗？'
          break;
          case '02':
            reqUrl = requestUrl.closeTakeaway
            tipMsg = '禁用外卖成功'
            optTip = '确定要将所选门店禁用外卖吗？'
          break;
          case '30':
            reqUrl = requestUrl.openExpress
            tipMsg = '启用配送成功'
            optTip = '确定要将所选门店启用配送吗？'
          break;
          case '03':
            reqUrl = requestUrl.closeExpress
            tipMsg = '禁用配送成功'
            optTip = '确定要将所选门店禁用配送吗？'
          break;
        }
        this.$confirm(optTip, '温馨提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.isDisabled = true
          this.$http.post(reqUrl, {storeCodes: storeCodes}).then(res => {
            this.isDisabled = false
            if(res.code === 0) {
              this.$message.success(tipMsg)
              this.doSearchHandel()
              return
            }
            throw new Error(res.msg)
          }).catch(e => {
            this.isDisabled = false
            this.$notify.error({
              title: '提示',
              message: e.message
            })
          })
        }).catch(() => {});
      },
      showSetFrightDialog() {
        this.dialogFormVisible = true
      },
      doSetFreightHandel() {
        if(!this.setFreightParams.basicShippingFee && this.setFreightParams.basicShippingFee != 0) {
          this.$message.warning("请输入基础运费金额")
          return
        }else {
          if(!salePriceReg.test(this.setFreightParams.basicShippingFee)) {
            this.$message.warning('基础运费金额必须是大于等于0，小数保留小数点后2位') 
            return
          }
        }
        if(!this.setFreightParams.freeShippingAmount && this.setFreightParams.freeShippingAmount != 0) {
          this.$message.warning("请输入单笔订单满金额")
          return
        }else {
          if(!salePriceReg.test(this.setFreightParams.freeShippingAmount)) {
            this.$message.warning('单笔订单满金额必须是大于等于0，小数保留小数点后2位') 
            return
          }
        }
        this.setFreightParams.storeCodes = this.checkedList.map(item => {
          return item.storeNo
        })
        this.isDisabled = true
        this.$http.post(requestUrl.expressMoneyConfig, { ...this.setFreightParams }).then(res => {
          setTimeout(() => {
            this.isDisabled = false
          }, 800)
          if(res.code === 0) {
            this.$message.success("运费设置成功")
            this.dialogFormVisible = false
            this.doSearchHandel()
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.isDisabled = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      }
    }
    
  }
</script>


